import React, { useState, useEffect, useMemo } from "react";
import axios from "axios";
import {Container, Pagination} from "react-bootstrap";

function App() {
    const [rows, setRows] = useState([])
    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/rows/?offset=" + pageStart
        }).then(response => {
            setRows(response.data)
        })
    }, [])

    const [pageActive, setPageActive] = useState(0)
    const pageLimit = 4
    const {pageItems, pageStart, pageEnd} = useMemo(() => {
        Math.ceil(rows.count / pageLimit)
        let pageItems = []
        for (let i = 0; i < pageLimit; i++) {
            pageItems.push(
                <Pagination key={i} active={i === pageActive} onClick={() => setPageActive(i)}>
                    {i + 1}
                </Pagination>
            )
        }
        const pageStart = pageActive * pageLimit
        const pageEnd = pageStart + pageLimit
        return{
            pageItems,
            pageStart,
            pageEnd,
        }
    }, [pageActive, rows.count])


    return(
        <Container>
            <div className="App">
                <h1>Table</h1>
                <table>
                    <tr>
                        <th>Дата</th>
                        <th>Название</th>
                        <th>Количество</th>
                        <th>Расстояние</th>
                    </tr>
                    {rows.results.map(row => (
                        <tr key={row.id}>
                            <td>{row.date}</td>
                            <td>{row.title}</td>
                            <td>{row.count}</td>
                            <td>{row.distance}</td>
                        </tr>
                    ))}
                </table>
            </div>
            <Pagination>
                {pageItems}
            </Pagination>
        </Container>
    );
}

export default App;
